import React from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { CLASS_NAME, PlanoContaReferencial } from '../../../../models/dtos/mcg/planoContaReferencial';
import PlanoContaMCGReferencialInstituicaoIS from '../../../components/mcg/planoContaMCGReferencialInstituicao/PlanoContaMCGReferencialInstituicaoIS';

interface PlanoContaReferencialEditViewProps extends EditViewProps<PlanoContaReferencial> {}

const PlanoContaReferencialEditView: React.FC<PlanoContaReferencialEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<PlanoContaReferencial>({
    defaultValues: { ...selectedItem, ...{ analitica: selectedItem.analitica === 'T' } },
    mode: 'onBlur',
  });

  const contaFormatter = {
    maxLength: 40,
    minLength: 1,
    pattern: {
      message: 'Somente números de [0-9] e os caracteres especiais [./-] são aceitos',
      value: new RegExp(`[0-9./-]`),
    },
    required: true,
  };

  const beforePersist = (formData: PlanoContaReferencial): PlanoContaReferencial => {
    // Ajustando os valores do switch para enviar somente 'T' ou 'F'
    // @ts-ignore
    formData.analitica = formData.analitica === true ? 'T' : 'F';
    return formData;
  };

  return (
    <WindowComponent title="Manutenção de Plano de Contas Referencial">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} beforePersist={beforePersist}>
        <Row>
          <Col>
            <PlanoContaMCGReferencialInstituicaoIS
              formProps={{ ...formProps, validation: { required: true } }}
              id="instituicao"
              label="instituicao"
              placeholder="Informe a Instituição"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="conta"
              isPrependLabel={true}
              formProps={{ ...formProps, validation: { ...contaFormatter } }}
              label="Conta"
              placeholder="Informe a Conta"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="descricao"
              isPrependLabel={true}
              formProps={{ ...formProps, validation: { required: true, maxLength: 60 } }}
              label="Descricão"
              placeholder="Informe a Descriçào"
            />
          </Col>
        </Row>
        <Row className="mb-3">
          <Col>
            <CustomInput type="switch" label="Analítica" name="analitica" id="analitica" innerRef={formProps.register} />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="observacao"
              label="Observação"
              formProps={{ ...formProps, validation: { maxLength: 500 } }}
              placeholder={'informe a Observação'}
              isPrependLabel={true}
              type={InputType.TEXT_AREA}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default PlanoContaReferencialEditView;
